body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f1ea;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.popup-content {
    width: 50vw;
    display: flex;
    align-items: center;
    flex-direction: column;


}
#exit_button{
    font-size: 1.5rem;
    border-color: transparent;
    outline: transparent;
    width:1rem;
    margin-top: 3vh;
    left:0;
    border-radius:100px;
    margin-left: 3vh;
    background-color:white;
    overflow:hidden;
    padding:0.5rem;
    z-index: 100;
    position: absolute;
}
#text_popup_title{
    font-size: 1.2rem;
    font-family: 'Open Sans Hebrew Condensed', serif;
    color: #4b5556;
    font-weight: bold;
}
#text_popup{
    font-size: 1.2rem;
    font-family: 'Open Sans Hebrew Condensed', serif;
    color: #4b5556;
    text-align: right;
    width:75%;
    margin-bottom: 0.5rem;

}


@media only screen and (max-width: 600px) {
    .popup-content {
        width: 95vw;

    }
    #exit_button{

        width:0.8rem;
        margin-top: 2vh;
        padding:0.3rem;
        margin-left: 2vh;

    }

}

