#category_main_div{
    display: flex;
    flex:1 1;
    flex-direction: row;
    margin-bottom: 2rem;
    width: 30vw;

}
.image_on_category_button{
    width:100%;
    align-items: center;
    justify-content: center;
}
.category_button{
    background-color: transparent;
    border: none;
    align-items: center;
    justify-content: center;
    display: flex;
    flex:1 1;
    outline: transparent;
}
.text_on_category_button{
    font-size:1.2vw;
    position: absolute;
    display: flex;
    margin-top: 1%;
    margin-right: 0.5%;
    font-family: 'Open Sans Hebrew Condensed', serif;
    flex:1 1;
    align-self:center;

}

@media only screen and (max-width: 600px) {
    .image_on_category_button{
        width: 100%;
    }

    #category_main_div{
        width: 95vw;
    }
    .text_on_category_button{
        font-size:0.9rem;
        margin-top: 0.5rem;
        margin-right: 1.5%;
    }
    .category_button{

        flex:1 1;
    }

}
@media only screen and (min-width: 600px) {
    #category_main_div{
        width: 45vw;
    }

}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
    #category_main_div{
        width: 35vw;
    }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
    #category_main_div{
        width: 30vw;
    }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {

    #category_main_div{
        width: 30%;
    }
}


.work_main_div{
    flex:1 1;
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    justify-content: center;


}
#left_side_work_container{
     margin-left: 1rem;
     margin-right: 5rem;

 }
.floating_text_on_image{
    font-size: 1.5rem;
    position: absolute;
    display: flex;
    font-family: 'Open Sans Hebrew Condensed', serif;
    flex:1 1;
    align-self: center;
    color: #4b5556;


}
#right_side_work_container{
    margin-right: 1rem;
    margin-left: 5rem;
}
.work_img{
    width:100%;
    border-radius: 2rem;
    opacity: var(--opacity);
}

#work_frame{
    border-radius: 5vw;
    display: flex;
    flex-direction: row;
}


@media only screen and (max-width: 600px) {
    #work_frame{
        flex-direction: column;
    }

    #right_side_work_container{

        width:90%;
        margin:0;
        align-self: center;


    }
    #left_side_work_container{

        width:90%;
        margin:0;
        align-self: center;

    }
    .work_main_div{
        margin-bottom: 1.2rem;
    }

}

